import React, { useState, useEffect } from 'react'; 
import './App.css';


const apiKey = "b64b371f-710e-4d0b-8d48-73544eec8f2f";
const url = "https://api.thecatapi.com/v1/images/search";

function App() {
  const [ catUrl, setCatUrl ] = useState('https://cdn2.thecatapi.com/images/2fk.png');// testing, function 

    useEffect(() => {
     console.log('hello');
     getCat();
    },[]);

    //

    const getCat = () => {
      console.log('hello world!');

      fetch(url, {
        headers: {
          'x-api-key': apiKey
        }
      })
      .then((res) => res.json())
      .then((cats) => {
        console.log('cats:', cats);
        const catUrl = cats[0].url;

        setCatUrl(catUrl);
      })
      .catch((error) => {
        console.log('Error:', error);
      });
    }

    console.log('catURL:', catUrl);

// JSX
return (
  <div className="app">
  
  <h1>Welcome to my catApp</h1>
  <img src={catUrl} alt=""/>
  <button onClick={getCat}>Get new random cat</button>
  </div>
  
);


}


export default App;